.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    text-align: center;
    height: 100dvh;
    min-height: 500px;
    
    .hero{
        max-width: 200px;
        border-radius: 100%;
    }

    span{
        display: flex;
        gap: 25px;
        justify-content: center;
        
        a{
        margin: 0;
        font-size: 30px;
        text-decoration: none;
        color: rgb(0, 0, 0);
        }
    }
    a{
        width: fit-content;
        align-self: center;
    }
    button{
        background-color: var(--btn-color);
        color: var(--btn-text-color);
        border: none;
        border-radius: 20px;
        width: 126px;
        height: 50px;
        font-size: 20px;
        font-weight: bold;
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        transition: box-shadow 200ms ease-in-out, transform 200ms ease-in-out;
    }
    button:hover{
        transform: scale(1.05);
    }
    button:active{
        transform: translateY(2px);
        box-shadow: 0 2px 20px rgba(0, 0, 0, 0.25);
    }

    .colorModeContainer{
        position: relative;
        a{
            font-size: 30px;
            margin-right: 40px;
            margin-top: 0;
            padding: 0;
        }
    }

    .colorMode {
        position: absolute;
        right: 0;
        width: 25px;
    }
    .colorMode:hover{
        cursor: pointer;
    }
    .description{
        max-width: 35ch;
    }
    .info{
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 20px;
    }
}

@media (width >= 800px) {
    .container{
        flex-direction: row-reverse;
        align-items: center;
        justify-content: space-evenly;

        .hero{
            max-width: 350px;
            width: 350px;
        }
    }
}

@media (width >= 1400px) {
    .container{
        .hero{
            max-width: 400px;
            width: 400px;
        }
    }
}