.container{
    display: flex;
    flex-direction: column;
    text-align: center;
    
    span{
        display: flex;
        align-items: center;
        gap: 5px;
    }
    .skillList{
        display: flex;
        justify-content: space-evenly;
        align-self: center;
        flex-wrap: wrap;
        row-gap: 40px;
        max-width: 26ch;
    }
    
}
hr{
        width: 100px;
        height: 1px;
        border: none;
        background-color: var(--text-color);
        margin: 30px auto;
    }
@media (width >= 800px){
    .skillList{
        max-width: fit-content;
        gap: 30px;
        p{
            font-size: 20px;
        }
        
    }
    hr{
            width: 300px;
        }
}